import React, {Component} from "react";
import "./Footer.scss"
import {APP_NAME} from "../../constants";
import * as translator from "../../translate";

class Footer extends Component {

    state = {
        language: "en",
        availableLanguages: [
            {code: "en", title: "English"},
            {code: "zh", title: "Chinese - Simple"},
            {code: "zh-traditional", title: "Chinese - Traditional"},
        ],
        languageCodes: {
            "en": "en",
            "en-US": "en",
            "zh": "zh",
            "zh-hk": "zh",
            "zh-sg": "zh",
            "zh-tw": "zh",
        }

    };

    constructor(props) {
        super(props);
        translator.setElementKey("ling-translate");
    }

    componentDidMount() {

        let browserLanguage = window.navigator.language;
        this.setGlobalLanguage(this.state.languageCodes[browserLanguage]);
    }

    setLanguage = (event) => {
        let code = event.target.value;
        this.setGlobalLanguage(code);

    };

    setGlobalLanguage = (code) => {
        this.setState({language: code});
        translator.translateContent(code);
    };

    render() {

        const {availableLanguages, language} = this.state;

        return (
            <div id="FooterComponent">

                <h2 className="color-white">{APP_NAME}</h2>

                <h4 id="languageSelectLabel">Language</h4>

                <select value={language} id="languageSelect" onChange={this.setLanguage}>
                    {availableLanguages.map((lang, key) => {
                        return <option key={key} value={lang.code}>{lang.title}</option>
                    })}
                </select>

            </div>
        )
    }
}

export default Footer;